import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "@/assets/css/common/index.scss" // css
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css' //element
import '@/assets/icon' // svg
import Vant from 'vant';
import 'vant/lib/index.css'; // vant

// 
import AOS from "aos";
import "../node_modules/aos/dist/aos.css";
// AOS.init({
//   offset: 0,
//   duration: 600, //
//   easing: 'ease-in-sine',
//   delay: 100
// })
AOS.init({
  // 
  disable: false, //  'phone', 'tablet', 'mobile', boolean,   
  startEvent: 'DOMContentLoaded', // 
  initClassName: 'aos-init',
  animatedClassName: 'aos-animate',
  useClassNames: false,
  disableMutationObserver: false,
  debounceDelay: 50, // 
  throttleDelay: 99, //  

  offset: 0, // 
  delay: 0, // 
  duration: 2000, // 
  easing: 'ease', // easing
  once: false, // ,
  mirror: false, // 
  anchorPlacement: 'top-bottom', // ,
});



Vue.use(AOS)
Vue.use(Vant)
Vue.use(ElementUI)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')